.main {
    $main: &;
    // padding: 0 1.5rem 2rem 1.5rem; 

    @include desktop {
        display: flex;
        gap: 6rem;
    }

    &__section {
        &__articles {
            &__list {
                &:nth-child(even) {
                    #{$main}__section__articles__list__link__article {
                        flex-direction: row-reverse;
                    }
                }
                &__link {
                    &__article {
                        margin-top: 2.5rem;

                        @include tablet {
                            @include displayFlex(row, center, center);
                            gap: 2rem;
                            // &:nth-child(even) {
                            //     display: flex;
                            //     flex-direction: row-reverse;
                            //     background-color: red;
                            // }
                        }
                        

                        &__image {
                            width: 100%;
                            max-height: 20rem;
                            border-radius: 1rem;

                            @include tablet {
                                width: 50%;
                            }
                        }
                        
                        &__container {
                            &__title {
                                margin-top: 0.5rem;
                                color: $text-color;
                                font: $bold-roboto-font-24px;
                                line-height: 1.6rem;
                            }
            
                            &__content, &__quote {
                                margin: 0.5rem 0;
                                color: $content-color;
                                font: $regular-roboto-font-14px;
                                line-height: 1.2rem;
                                overflow: hidden;
                                -webkit-box-orient: vertical;
                                display: -webkit-box;
                                -webkit-line-clamp: 3;
                            }
            
                            &__date {
                                font: $light-roboto-font-14px;
                                color: $date-color;
                            }

                        }
                    }
                }
            }
        }
    }

    &__aside {

        @include desktop {
            background-color: rgb(245, 245, 245);
            padding: 1rem;
        }

        &__title {
            margin-top: 2.5rem;
            font: $bold-roboto-font-24px;
            color: $text-color;

            @include desktop {
                text-align: center;
            }

            &::first-letter {
                text-transform: capitalize;
            }
        }

        &__link {
            &__article {
                @include displayFlex(row, center, flex-start);
                gap: 1rem;
                margin-top: 1rem;
                color: $text-color;

                @include tablet {
                    @include displayFlex(row, flex-start, center);
                }

                @include desktop {
                    flex-direction: column;
                }

                &__image {
                    width: 10rem;
                    border-radius: 0.8rem;
                }

                &__text-container {
                    @include displayFlex(column, space-between, flex-start);
                    &__title {
                        font: $bold-roboto-font-14px;
                        line-height: 1rem;

                        @include tablet {
                            font-size: 1rem;
                        }
                    }

                    &__date {
                        margin-top: 0.5rem;
                        font: $light-roboto-font-12px;
                        color: $date-color;
                    }
                }
            }
        }
    }
}